.typewriter h1 {
    font-size: 49px;
    width: 58%;
    font-family: monospace;
    overflow: hidden;
    border-right: .15em solid orange;
    white-space: nowrap;
    margin: 0 auto;
    letter-spacing: .15em;
    animation:
        typing 3.5s steps(30),
        blink-caret .5s infinite;
}


@keyframes typing {
    from {
        width: 0
    }

    to {
        width: 59%
    }
}


@keyframes blink-caret {

    from,
    to {
        border-color: transparent
    }

    50% {
        border-color: orange
    }
}

