/*scroll*/

.scrollTop {
    position: fixed;
    width: 32px;
    right: 20px;
    bottom: 20px;
    align-items: center;
    height: 20px;
    justify-content: center;
    z-index: 1000;
    cursor: pointer;
    animation: fadeIn 0.3s;
    transition: opacity 0.4s;
    opacity: 0.5;
    color: blue;
}

.scrollTop:hover {
    opacity: 1;
}

@keyframes fadeIn {
    0% {
        opacity: 0;
    }

    100% {
        opacity: 0.5;
    }
}

.effect {
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 19px;
}

.varrius a {
    font-size: 25px;
    margin: 10px;
    padding: 10px;
    transition: border 500ms, font-size 600ms;
    color: white;
}

.varrius a:hover {
    font-size: 28px;
    border: 1px dashed white;
    padding: 10px;
    border-radius: 50px;
}

